.header {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;

  z-index: 10;
  visibility: hidden;
  transition: visibility 0.3s step-end;
}

.header._active {
  visibility: visible;
  transition: visibility 0s step-end;
}

.headerBg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  background: #020202d0;
  transition: opacity 0.3s linear;
  opacity: 0;
}

.header._active .headerBg {
  opacity: 1;
}

.header._active .menu {
  transform: translateX(0);
}

.menu {
  position: absolute;
  top: 10px;
  height: calc(100% - 20px);
  transform: translateX(-100%);
  transition: transform 0.3s linear;

  background: rgb(226, 226, 226);
  padding: 50px 20px 20px 20px;

  box-shadow: 0 0 20px -20px black;
  border-radius: 0 10px 10px 0;

  display: flex;
  flex-direction: column;
  justify-content: start;
  gap: 12px;
  overflow-y: scroll;


  -ms-overflow-style: none;
  /* IE и Edge */
  scrollbar-width: none;
  /* Firefox */
}

.menu::-webkit-scrollbar {
  display: none;
}

.munuItem {}

.munuItem a {
  white-space: nowrap;
  color: #020202;
  font-size: 22px;
  font-weight: 500;
  transition: all 0.3s ease;
  position: relative;
}

.munuItem a::before {
  content: "";
  position: absolute;
  bottom: 0;
  height: 1px;
  width: 100%;
  background-color: #020202;

  opacity: 0;
  transition: opacity 0.3s ease;
}

.munuItem a:hover::before {
  opacity: 1;
}

.burger {
  cursor: pointer;

  position: fixed;
  top: 20px;
  left: 20px;
  z-index: 11;

  height: 26px;
  width: 32px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.line {
  display: block;
  height: 4px;
  width: 100%;
  border-radius: 10px;
  background: #0e2431;
}

.line2 {
  transition: transform 0.2s ease-in-out;
}

.line1 {
  transform-origin: 0% 0%;
  transition: transform 0.4s ease-in-out;
}

.line3 {
  transform-origin: 0% 100%;
  transition: transform 0.4s ease-in-out;
}

.burger._active .line2 {
  transform: scaleY(0);
}

.burger._active .line3 {
  transform: rotate(-45deg);
}

.burger._active .line1 {
  transform: rotate(45deg);
}

._more {
  position: fixed;
  padding: 4px;
  border-radius: 5px;
  left: 50%;
  transform: translateX(-50%);
  bottom: 10px;
}

body:has(.header._active) {
  overflow-y: hidden;
}