@charset "UTF-8";

@font-face {
  font-family: 'PTMono';
  font-display: swap;
  src:
    url('./assets/fonts/pt-mono_regular.woff2') format('woff2'),
    url('./assets/fonts/pt-mono_regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'PTMono';
  font-display: swap;
  src:
    url('./assets/fonts/pt-mono_bold.woff2') format('woff2'),
    url('./assets/fonts/pt-mono_bold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("./assets/fonts/Inter-Regular.woff2") format("woff2"), url("./assets/fonts/Inter-Regular.woff") format("woff");
}

@font-face {
  font-family: 'Roboto Mono';
  font-display: swap;
  src: url('./assets/fonts/RobotoMono-Regular.woff2') format('woff2'), url('./assets/fonts/RobotoMono-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Roboto Mono';
  font-display: swap;
  src: url('./assets/fonts/RobotoMono-Italic.woff2') format('woff2'), url('./assets/fonts/RobotoMono-Italic.woff') format('woff');
  font-weight: 400;
  font-style: italic;
}

@font-face {
  font-family: 'Roboto Mono';
  font-display: swap;
  src: url('./assets/fonts/RobotoMono-Medium.woff2') format('woff2'), url('./assets/fonts/RobotoMono-Medium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
}

/*Обнуление*/
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-family: "Inter";
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

/*--------------------*/

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}